                      <ul class="faq-list">
                        <li>
                          <div
                            data-bs-toggle="collapse"
                            class="collapsed question"
                            href="#faq1"
                            @click="toggleChevron2"
                          >
                            O que é licitação?
                            <i
                              class="fa fa-chevron-down icon-show"
                              aria-hidden="true"
                              v-bind:class="{ 'd-none': showIcon2 }"
                            ></i>
                            <i
                              class="fa fa-chevron-up icon-close"
                              aria-hidden="true"
                              v-bind:class="{ 'd-none': !showIcon2 }"
                            ></i>
                          </div>
                          <div
                            id="faq1"
                            class="collapse"
                            data-bs-parent=".faq-list"
                          >
                            <p class="text-justify">
                              Licitação é procedimento administrativo formal em
                              que a Administração Publica convoca, por meio de
                              condições estabelecidas em ato próprio (edital ou
                              convite), empresas interessadas na apresentação de
                              propostas para o oferecimento de bens e serviços.
                              Objetiva garantir a observância do principio
                              constitucional da isonomia e a selecionar a
                              proposta mais vantajosa para a Administração, de
                              maneira a assegurar oportunidade igual a todos os
                              interessados e a possibilitar o comparecimento ao
                              certame do maior numero possível de concorrentes.
                            </p>
                          </div>
                        </li>

                        <li>
                          <div
                            data-bs-toggle="collapse"
                            href="#faq2"
                            class="collapsed question"
                            @click="toggleChevron1"
                          >
                            Porque fazer licitação?
                            <i
                              class="fa fa-chevron-down icon-show"
                              aria-hidden="true"
                              v-bind:class="{ 'd-none': showIcon1 }"
                            ></i>
                            <i
                              class="fa fa-chevron-up icon-close"
                              aria-hidden="true"
                              v-bind:class="{ 'd-none': !showIcon1 }"
                            ></i>
                          </div>
                          <div
                            id="faq2"
                            class="collapse"
                            data-bs-parent=".faq-list"
                          >
                            <p>
                              A Constituição Federal, fundamentou em seu art.
                              37, inciso XXI, a obrigatoriedade do procedimento
                              licitatório nas contratações da administração
                              pública, e a Lei 8.666/93, art. 2º, regulamento os
                              procedimentos, a fim de valer a obrigatoriedade
                              dos mesmos.
                            </p>
                          </div>
                        </li>

                        <li>
                          <div
                            data-bs-toggle="collapse"
                            href="#faq3"
                            class="collapsed question"
                            @click="toggleChevron"
                          >
                            Quais leis regulamentam?
                            <i
                              class="fa fa-chevron-down icon-show"
                              aria-hidden="true"
                              v-bind:class="{ 'd-none': showIcon }"
                            ></i>
