                            ></i>
                          </div>
                          <div
                            id="faq3"
                            class="collapse"
                            data-bs-parent=".faq-list"
                          >
                            <p>
                              As licitações são regulamentadas pela Lei 8.666 de
                              21 de junho de 1993, também chamada Lei Geral das
                              Licitações, que normatiza as concorrências,
                              tomadas de preços, convites, leilão e concursos;
                              os pregões foram formalizados pela lei 10.520 de
                              17 de julho de 2002.
                            </p>
                          </div>
                        </li>

                        <li>
