                            ></i>
                          </div>
                          <div
                            id="faq4"
                            class="collapse"
                            data-bs-parent=".faq-list"
                          >
                            <p>
                              Todos os órgãos da Administração Pública direta,
                              os fundos especiais, as autarquias, as fundações
                              públicas, as empresas públicas, as sociedades de
                              economia mista e demais entidades controladas
                              direta ou indiretamente pela União, Estados,
                              Distrito Federal e Municípios estão obrigados à
                              licitação.
                            </p>
                          </div>
